"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

interface Product {
  id: number
  name: string
  price: number
  image: string
  description: string
  category: string
  isNew: boolean
}

export default function NewArrivals() {
  const [products] = useState<Product[]>([
    {
      id: 1,
      name: "Vestido de Gala",
      price: 299.99,
      image: "https://images.pexels.com/photos/1755428/pexels-photo-1755428.jpeg",
      description: "Elegante vestido de gala con detalles exclusivos",
      category: "Vestidos",
      isNew: true
    },
    {
      id: 2,
      name: "Conjunto Deportivo",
      price: 189.99,
      image: "https://images.pexels.com/photos/2529148/pexels-photo-2529148.jpeg",
      description: "Conjunto deportivo moderno y cómodo",
      category: "Deportivo",
      isNew: true
    },
    {
      id: 3,
      name: "Cartera de Cuero",
      price: 159.99,
      image: "https://images.pexels.com/photos/1152077/pexels-photo-1152077.jpeg",
      description: "Cartera de cuero genuino con diseño moderno",
      category: "Accesorios",
      isNew: true
    }
  ])

  const handleWhatsAppQuote = (product: Product) => {
    const message = `Hola, me interesa este nuevo producto:\n\n` +
      `Producto: ${product.name}\n` +
      `Precio: S/. ${product.price}\n` +
      `Categoría: ${product.category}\n\n` +
      `¿Podrían brindarme más información?`
    const whatsappUrl = `https://wa.me/519XXXXXXXX?text=${encodeURIComponent(message)}`
    window.open(whatsappUrl, "_blank")
  }

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      {/* Header Section */}
      <div className="container mx-auto px-4 mb-12">
        <div className="text-center">
          <h1 className="text-4xl font-bold mb-4">Nuevos Ingresos</h1>
          <p className="text-gray-600 max-w-2xl mx-auto">
            Descubre nuestras últimas novedades en moda y accesorios. 
            Productos exclusivos seleccionados especialmente para ti.
          </p>
        </div>
      </div>

      {/* Products Grid */}
      <div className="container mx-auto px-4">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {products.map((product) => (
            <Card 
              key={product.id} 
              className="overflow-hidden group hover:shadow-xl transition-shadow duration-300"
            >
              <div className="relative">
                {/* New Label */}
                <div className="absolute top-4 right-4 z-10">
                  <span className="bg-pink-600 text-white px-3 py-1 rounded-full text-sm font-medium">
                    Nuevo
                  </span>
                </div>

                {/* Product Image */}
                <div className="relative h-[300px] overflow-hidden">
                  <div 
                    className="absolute inset-0 bg-cover bg-center transform group-hover:scale-110 transition-transform duration-500"
                    style={{
                      backgroundImage: `url(${product.image})`
                    }}
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
                  <div className="absolute bottom-0 left-0 right-0 p-4 text-white transform translate-y-full group-hover:translate-y-0 transition-transform duration-300">
                    <p className="text-sm">{product.description}</p>
                  </div>
                </div>

                {/* Product Info */}
                <div className="p-6">
                  <div className="flex justify-between items-start mb-2">
                    <h3 className="text-xl font-semibold">{product.name}</h3>
                    <span className="text-sm text-gray-500">{product.category}</span>
                  </div>
                  <div className="flex items-center justify-between mt-4">
                    <span className="text-2xl font-bold text-pink-600">
                      S/. {product.price}
                    </span>
                    <Button 
                      onClick={() => handleWhatsAppQuote(product)}
                      className="bg-pink-600 hover:bg-pink-700"
                    >
                      Cotizar
                    </Button>
                  </div>
                </div>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </div>
  )
}
